import axios from 'axios'

const url = '/api/posts/'

class PostService {
  static getPosts() {
    return axios
      .get(url)
      .then((res) => {
        const data = res.data
        return data
      })
      .catch((err) => {
        throw err
      })
  }

  static insertPosts(key, value) {
    return axios.post(url, { key, value })
  }

  static deletePosts(id) {
    return axios.delete(`${url}${id}`)
  }
}

export default PostService
