<template>
  <div class="container">
    <h1>Latest posts</h1>
    <div class="create-post">
      <label for="create-post">Add post</label>
      <input type="text" id="key" v-model="key" placeholder="key">
      <input type="text" id="value" v-model="value" placeholder="value">
      <button v-on:click="createPost">Post!</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="post-container">
      <div class="post"
          v-for="(post, index) in posts"
          v-bind:item="post"
          v-bind:index="index"
          v-bind:key="post"
          >
          <p class="text">key: {{ index }} value: {{ post }}</p>
        </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService'

export default {
  name: 'PostComponent',
  data() {
    return {
      posts: {},
      error: '',
      key: '',
      value: ''
    }
  },
  async created() {
    try{
      this.posts = await PostService.getPosts()
    }
    catch(err){
      this.err = err.message
    }
  },
  methods: {
    async createPost(){
      console.log('create post')
      await PostService.insertPosts(this.key, this.value)
      this.posts = await PostService.getPosts()
    }
  }
}
</script>

